import React, {Suspense, lazy} from 'react';
import './App.css';

import {
  HashRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

const UpdateEmployee = lazy(() => import('./UpdateEmployee'));
const HeaderComponent = lazy(() => import('./HeaderComponent'));
const EmployeeDetail = lazy(() => import('./EmployeeDetail'));
const ListEmployee = lazy(() => import('./ListEmployee'));


const App=()=> {
  return (
    
   <div className="App">
   	   
        <Suspense fallback={<div>Loading...</div>}>
                <HeaderComponent />
        <Router>
         
              <Switch>
              
                <Route exact path="/employees" component={ListEmployee} />
              
                <Route exact path="/employeeform">
                  <EmployeeDetail />
                </Route>
                
                <Route exact path="/updateemployee/:id">
                	<UpdateEmployee />
                </Route>

              </Switch>    
        </Router>

         </Suspense>
  </div>
  );
}

export default App;
